@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2"),
    url("../fonts/opensansbold.woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2"),
    url("../fonts/opensans.woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2"),
    url("../fonts/opensanslight.woff");
}

.header-page {
  width: inherit;

  @media (min-width: @desktop-width) {
    // display: flex;
    // width: 940px;
    // margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: @desktop-width) {
  .header-page--closed .header-page__navigation-list {
    display: none;
  }

  .header-page--opened .header-page__navigation-list {
    display: flex;
  }
}

