.header-page__container {
  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 940px;
    margin: 0 auto;
  }
}

.header-page__wrapper {
  display: flex;
  background-color: rgba(0, 0, 0, 0.3);
}

.header-page__wrapper--width {
  @media (min-width: @desktop-width) {
    background-color: transparent;
  }
}

.header-page__logo-text {
  &:hover path {
    fill: @hover-color;
  }

  &:active path {
    fill: @active-color;
    opacity: 0.3;
  }
}

.header-page__logo-tablet {
  display: none;
}

.header-page__logo-desktop {
  display: none;
}

@media (min-width: @tablet-width) {
  .header-page__logo-text {
    display: none;
  }

  .header-page__logo-tablet {
    display: inline;
    margin: 27px 0 43px 20px;

    &:hover path {
      fill: @hover-color;
    }

    &:active path {
      fill: @active-color;
      opacity: 0.3;
    }
  }
}

@media (min-width: @desktop-width) {
  .header-page__logo-text {
    display: none;
  }

  .header-page__logo-tablet {
    display: none;
  }

  .header-page__logo-desktop {
    display: inline;
    padding-bottom: 40px;
    padding-top: 27px;
    // width: 940px;
    // margin: 0 auto;
  }
}

.header-page__logo-text--phone {
  margin: 22px 21px 20px 20px;
}

.header-page__wrapper .header-page--opened {
  background-image: none;

  &:hover {
    background-image: none;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 28px;
    height: 3px;
    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover::before,
  &:hover::after {
    background-color: #d22856;
  }

  &:active::before,
  &:active::after {
    background-color: fade(#d22856, 30%);
  }
}

.header-page--nojs .header-page__navigation-list {
  display: flex;
}

.header-page__navigation-toggle--phone {
  width: 100px;
  padding: 0;
  margin: 0;
  margin-left: auto;
  border: none;
  background-image: url("../img/open-menu-btn.png");
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    background-image: url("../img/open-menu-hover.png");
  }

  &:active {
    background-image: url("../img/open-menu-hover.png");
    opacity: 0.3;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

