.header-page__navigation-list {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 320px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  z-index: 2;

  margin-block-start: 0;
  margin-block-end: 0;
  padding-inline-start: 0;

  @media (min-width: @tablet-width) {
    width: 660px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 150px 187px 145px 138px;
    justify-items: start;
    width: auto;
  }
}

.header-page__navigation--column {
  @media (min-width: @desktop-width) {
    display: flex;
    width: 620px;
    align-items: center;
  }
}

.header-page__navigation-item {

  width: inherit;
  color: #ffffff;
  text-align: center;

  &:nth-of-type(4n) {
    @media (min-width: @desktop-width) {
      margin-left: auto;
    }
  }

  a {
    display: inline-block;
    width: inherit;
    font-family: "Open Sans", serif;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
    color: inherit;
    text-decoration: none;
    text-align: inherit;
    padding-top: 16px;
    padding-bottom: 15px;
    text-transform: uppercase;
    background: #283645;
    // box-shadow: 0 -1px 0 0 #000000, inset 0 1px 0 0 rgba(255, 255, 255, 0.15);

    @media (min-width: @desktop-width) {
      background: none;
      line-height: 18px;
      font-weight: 400;
      text-transform: none;
    }
  }
}
